import { fetchTransactions } from "@/api/transaction";

const transactionStore = {
  namespaced: true,

  state: {},

  mutations: {},

  actions: {
    FetchTransactions({ commit, state }, urlParams = null) {
      return new Promise((resolve, reject) => {
        fetchTransactions(urlParams)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default transactionStore;



