import {
  fetchSuppliers,
  fetchSupplier,
  addSupplier,
  updateSupplier,
  deleteSupplier
} from "../../api/supplier";
import { LOGIN_TOKEN } from "@/constants";
import router from "@/router";

const supplierStore = {
  state: {},

  mutations: {},

  actions: {
    FetchSuppliers({ commit, state }, urlParams=null) {
      return new Promise((resolve, reject) => {
        fetchSuppliers(urlParams)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    AddSupplier({ commit }, supplierData) {
      return new Promise((resolve, reject) => {
        addSupplier(supplierData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    UpdateSupplier({ commit }, supplierData) {
      return new Promise((resolve, reject) => {
        updateSupplier(supplierData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    DeleteSupplier({ commit }, supplierIds) {
      return new Promise((resolve, reject) => {
        deleteSupplier(supplierIds)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default supplierStore;



