import { loginByUsername, logout } from "../../api/auth";
import { fetchAccesses } from "../../api/access";
import { LOGIN_TOKEN } from "@/constants";
import router from "@/router";

const userStore = {
  state: {},

  mutations: {},

  actions: {
    FetchAccesses({ commit, state }) {
      return new Promise((resolve, reject) => {
        fetchAccesses()
          .then(res => {
            const data = res.data;
            const accesses = data.data.accesses;
            resolve(accesses);
          })
          .catch(error => {
            console.log("Error fetch user: ", error);
            reject(error);
          });
      });
    }
  }
};

export default userStore;



