import {
  fetchProducts,
  fetchProduct,
  addProduct,
  updateProduct,
  deleteProduct
} from "@/api/product";
import { fetchPosProducts, sellProduct } from "@/api/pos";
import { SELECTED_SHOP } from "@/constants";
import router from "@/router";
import { arrayOfObjToObject } from "@/utils/arrayToObject";
import { calcTotalOrderValue } from "@/api/pos";

const posStore = {
  namespaced: true,

  state: {
    selectedShop: null,
    dialogProductAdd: {
      isOpen: false
    },
    products: [],
    categories: [],
    orders: [],
    selectedProduct: {
      productId: null,
      name: "",
      stock: 0,
      prices: [],
      selectedPrice: 0,
      discount: {
        type: "value",
        value: 0
      },
      qty: 1
    },
    totalOrderValue: 0,
    totalPayment: 0
  },

  mutations: {
    TOGGLE_POS_DIALOG_PRODUCT_ADD: state => {
      state.dialogProductAdd.isOpen = !state.dialogProductAdd.isOpen;
    },
    SET_PRODUCTS: (state, products) => {
      state.products = products;
    },
    SET_PRODUCT_CATEGORIES: (state, categories) => {
      state.categories = categories;
    },
    SET_ORDERS: (state, orders) => {
      state.orders = orders;
    },
    ADD_ORDER: (state, order) => {
      let prevOrder = state.orders.filter(prevOrder => {
        const sameProduct = prevOrder.productId === order.productId;
        const samePrice = prevOrder.selectedPrice === order.selectedPrice;
        const isDiscountZero =
          prevOrder.discount.value === 0 && order.discount.value === 0;

        return sameProduct && samePrice && isDiscountZero;
      })[0];

      let prevOrderWithDiscount = state.orders.filter(prevOrder => {
        const sameProduct = prevOrder.productId === order.productId;
        const samePrice = prevOrder.selectedPrice === order.selectedPrice;
        const sameDiscountType =
          prevOrder.discount.type === order.discount.type;
        const sameDiscountValue =
          prevOrder.discount.value === order.discount.value;

        return (
          sameProduct && samePrice && sameDiscountType && sameDiscountValue
        );
      })[0];

      if (prevOrder) {
        prevOrder.qty += order.qty;
      } else if (prevOrderWithDiscount) {
        prevOrderWithDiscount.qty += order.qty;
      } else {
        state.orders.push(order);
      }

      // recalculate total_payment
      state.totalOrderValue = calcTotalOrderValue(state.orders);
    },
    UPDATE_ORDER: (state, { index, order }) => {
      state.orders[index] = order;
      state.totalOrderValue = calcTotalOrderValue(state.orders);
    },
    DELETE_ORDER: (state, index) => {
      state.orders.splice(index, 1);
      state.totalOrderValue = calcTotalOrderValue(state.orders);
    },
    SET_SELECTED_PRODUCT: (state, product) => {
      const selectedPrice = product.prices ? product.prices[0].price : 0;
      console.log("selectedPrice =>", selectedPrice);
      state.selectedProduct = {
        ...state.selectedProduct,
        // ...product,
        productId: product.product_id,
        name: product.name,
        stock: product.stock,
        prices: product.prices,
        selectedPrice: selectedPrice
      };
    },
    SET_SELECTED_SHOP: (state, shop) => {
      state.selectedShop = shop;
      localStorage.setItem(SELECTED_SHOP, JSON.stringify(shop));
    },
    RESET_SELECTED_PRODUCT: state => {
      state.selectedProduct = {
        productId: null,
        name: "",
        stock: 0,
        prices: [],
        selectedPrice: 0,
        discount: {
          type: "value",
          value: 0
        },
        qty: 1
      };
    },
    SET_TOTAL_ORDER_VALUE: (state, totalOrderValue) => {
      state.totalOrderValue = totalOrderValue;
    },
    SET_TOTAL_PAYMENT: (state, totalPayment) => {
      state.totalPayment = totalPayment;
    }
  },

  actions: {
    FetchPosProducts({ commit, state }, urlParams = null) {
      return new Promise((resolve, reject) => {
        fetchPosProducts(urlParams)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    togglePosDialogProductAdd({ commit }) {
      commit("TOGGLE_POS_DIALOG_PRODUCT_ADD");
      commit("RESET_SELECTED_PRODUCT");
    },
    setProducts({ commit }, products) {
      commit("SET_PRODUCTS", products);
    },
    setProductCategories({ commit }, categories) {
      commit("SET_PRODUCT_CATEGORIES", categories);
    },
    addOrder({ commit }, order) {
      commit("ADD_ORDER", order);
    },
    updateOrder({ commit }, { index, order }) {
      commit("UPDATE_ORDER", { index, order });
    },
    deleteOrder({ commit }, index) {
      commit("DELETE_ORDER", index);
    },
    setSelectedProduct({ commit }, product) {
      commit("SET_SELECTED_PRODUCT", product);
    },
    setSelectedShop({ commit, dispatch }, shop) {
      commit("SET_SELECTED_SHOP", shop);
      dispatch("ResetPos");
    },
    setTotalOrderValue({ commit }, totalOrderValue) {
      commit("SET_TOTAL_ORDER_VALUE", totalOrderValue);
    },
    setTotalPayment({ commit }, totalPayment) {
      commit("SET_TOTAL_PAYMENT", totalPayment);
    },
    SellProduct({ commit }, payload) {
      return new Promise((resolve, reject) => {
        sellProduct(payload)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    ResetPos({ commit }) {
      commit("SET_ORDERS", []);
      commit("RESET_SELECTED_PRODUCT");
      commit("SET_TOTAL_ORDER_VALUE", 0);
      commit("SET_TOTAL_PAYMENT", 0);
    }
  },

  getters: {
    posDialogProductAdd: state => state.dialogProductAdd,
    posProducts: state => state.products,
    posCategories: state => state.categories,
    posOrders: state => state.orders,
    posSelectedProduct: state => state.selectedProduct,
    selectedShop: state => state.selectedShop
  }
};

export default posStore;



