import request from "@/utils/request.js";
import urlSerializer from "@/utils/urlSerializer";

export function fetchRoles(urlParams) {
  const urlParams_s = urlSerializer(urlParams);
  return request({
    url: "/get/role?" + urlParams_s,
    method: "get"
  });
}

export function fetchRole(roleId) {
  return request({
    url: "/get/role/" + roleId,
    method: "get"
  });
}

export function addRole(roleData) {
  const data = {
    ...roleData
  };
  return request({
    url: "/create/role",
    method: "post",
    data
  });
}

export function updateRole(roleData) {
  const { roleId, ...data } = roleData;
  return request({
    url: "/edit/role/" + roleId,
    method: "put",
    data
  });
}

export function deleteRole(roleIds) {
  const data = {
    role_ids: roleIds
  };
  console.log("data: ", data);
  return request({
    url: "/delete/role/",
    method: "delete",
    data
  });
}



