import {
  fetchProducts,
  fetchProduct,
  addProduct,
  updateProduct,
  deleteProduct,
  fetchProductStatistic,
} from "@/api/product";
import { LOGIN_TOKEN } from "@/constants";
import router from "@/router";

const productStore = {
  state: {},

  mutations: {},

  actions: {
    FetchProducts({ commit, state }, urlParams=null) {
      return new Promise((resolve, reject) => {
        fetchProducts(urlParams)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    FetchProduct({ commit, state }, productId) {
      return new Promise((resolve, reject) => {
        fetchProduct(productId)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    AddProduct({ commit }, productData) {
      return new Promise((resolve, reject) => {
        addProduct(productData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    UpdateProduct({ commit }, productData) {
      return new Promise((resolve, reject) => {
        updateProduct(productData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    DeleteProduct({ commit }, productIds) {
      return new Promise((resolve, reject) => {
        deleteProduct(productIds)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    FetchProductStatistic({ commit }, productId) {
      return new Promise((resolve, reject) => {
        fetchProductStatistic(productId)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default productStore;



