<template>
  <el-menu
    mode="vertical"
    :default-active="$route.path"
    class="el-menu-vertical"
    :collapse="sidebar.isCollapse"
    text-color="#cdcdcd"
    active-text-color="#fff">

    <template v-for="item in permission_routers" v-if="!item.hidden&&item.children">

      <router-link v-if="item.children.length===1 && !item.children[0].children&&!item.alwaysShow" :to="item.path+'/'+item.children[0].path" :key="item.children[0].name">
        <el-menu-item :index="item.path+'/'+item.children[0].path" >
          <i v-if="item.children[0].meta&&item.children[0].meta.icon" class="padding-6"><icon :name="item.children[0].meta.icon"></icon></i>
          <span slot="title" v-if="item.children[0].meta&&item.children[0].meta.title">{{item.children[0].meta.title}}</span>
        </el-menu-item>
      </router-link>

      <el-submenu v-else :index="item.name||item.path" :key="item.name">
        <template slot="title">
          <i v-if="item.meta&&item.meta.icon" class="padding-6"><icon :name="item.meta.icon"></icon></i>
          <span v-if="item.meta&&item.meta.title">{{item.meta.title}}</span>
        </template>

        <template v-for="child in item.children" v-if="!child.hidden">
          <sidebar-item :is-nest="true" class="nest-menu" v-if="child.children&&child.children.length>0" :routes="[child]" :key="child.path"></sidebar-item>

          <router-link v-else :to="item.path+'/'+child.path" :key="child.name">
            <el-menu-item :index="item.path+'/'+child.path" class="submenu-item">
              <i v-if="child.meta&&child.meta.icon" class="padding-6"><icon :name="child.meta.icon"></icon></i>
              <span slot="title" v-if="child.meta&&child.meta.title">{{child.meta.title}}</span>
            </el-menu-item>
          </router-link>
        </template>
      </el-submenu>

    </template>

  </el-menu>
</template>

<script>
import { mapGetters } from "vuex";
import router from "@/router";

export default {
  computed: {
    ...mapGetters(["permission_routers", "sidebar"])
  },
};
</script>

<style>
.el-menu-vertical {
  overflow-y: scroll;
  background-color: #262834;
}
@media only screen and (min-width: 990px) {
  .el-menu-vertical {
    min-width: 67px;
    overflow-y: scroll;
  }
}
.el-menu-item:focus,
.el-menu-item:hover {
  outline: 0;
  color: #fff !important;
  background-color: #262834;
}
.el-menu--collapse {
  width: 0px;
}
.el-menu-vertical:not(.el-menu--collapse) {
  width: 200px;
}
.submenu-item {
  background-color: #292B3B;
  /* padding-left: 52px !important; */
}
.el-submenu__title:hover {
  background-color: #262834;
  color: #fff !important;
}
.padding-6 {
  padding: 6px !important;
}
</style>




