import { SELECTED_SHOP } from "@/constants";

export function getSelectedShop() {
  const selectedShop = JSON.parse(localStorage.getItem(SELECTED_SHOP));
  if (selectedShop) {
    return selectedShop;
  } else {
    return null;
  }
}

export function getSelectedShopId() {
  const selectedShop = JSON.parse(localStorage.getItem(SELECTED_SHOP));
  if (selectedShop) {
    return selectedShop.shop_id;
  } else {
    return "";
  }
}

export function getSelectedShopName() {
  const selectedShop = JSON.parse(localStorage.getItem(SELECTED_SHOP));
  if (selectedShop) {
    return selectedShop.name;
  } else {
    return "Unknown Shop";
  }
}

export function removeSelectedShop() {
  localStorage.removeItem(SELECTED_SHOP);
}



