import request from "@/utils/request.js";
import urlSerializer from "@/utils/urlSerializer";

export function fetchUsers(urlParams) {
  const urlParams_s = urlSerializer(urlParams);

  return request({
    url: "/get/user?" + urlParams_s,
    method: "get"
  });
}

export function fetchUser(userId) {
  return request({
    url: "/get/user/" + userId,
    method: "get"
  });
}

export function addUser(userData) {
  const data = {
    ...userData
  };
  return request({
    url: "/create/user",
    method: "post",
    data
  });
}

export function updateUser(userData) {
  const { userId, ...data } = userData;
  return request({
    url: "/edit/user/" + userId,
    method: "put",
    data
  });
}

export function deleteUser(userIds) {
  const data = {
    user_ids: userIds
  };
  console.log("data: ", data);
  return request({
    url: "/delete/user/",
    method: "delete",
    data
  });
}

export function fetchUserStatistic(userId) {
  return request({
    url: "/get/user/" + userId + "/statistic",
    method: "get"
  });
}



