import {
  fetchShops,
  fetchShop,
  addShop,
  updateShop,
  deleteShop,
  fetchShopStatistic,
} from "@/api/shop";
import { LOGIN_TOKEN } from "@/constants";
import router from "@/router";

const shopStore = {
  state: {},

  mutations: {},

  actions: {
    FetchShops({ commit, state }, urlParams = null) {
      return new Promise((resolve, reject) => {
        fetchShops(urlParams)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    FetchShop({ commit, state }, shopId) {
      return new Promise((resolve, reject) => {
        fetchShop(shopId)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    AddShop({ commit }, shopData) {
      return new Promise((resolve, reject) => {
        addShop(shopData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    UpdateShop({ commit }, shopData) {
      return new Promise((resolve, reject) => {
        updateShop(shopData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    DeleteShop({ commit }, shopIds) {
      return new Promise((resolve, reject) => {
        deleteShop(shopIds)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    FetchShopStatistic({ commit }, shopId) {
      return new Promise((resolve, reject) => {
        fetchShopStatistic(shopId)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default shopStore;



