export const arrayToObject = function(array) {
  const obj = {};
  array.forEach(key => {
    if (Array.isArray(key)) {
      obj[key[0]] = key[1];
    } else {
      obj[key] = "";
    }
  });

  return obj;
};

const arrayOfObjToObject = function (arrayOfObj, key) {
  const new_obj = {};
  arrayOfObj.forEach(obj => {
    new_obj[obj[key]] = obj;
  });

  return new_obj;
};



