import {
  fetchRoles,
  fetchRole,
  addRole,
  updateRole,
  deleteRole
} from "../../api/role";
import { LOGIN_TOKEN } from "@/constants";
import router from "@/router";

const roleStore = {
  state: {},

  mutations: {},

  actions: {
    FetchRoles({ commit, state }, urlParams=null) {
      return new Promise((resolve, reject) => {
        fetchRoles(urlParams)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    AddRole({ commit }, roleData) {
      return new Promise((resolve, reject) => {
        addRole(roleData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    UpdateRole({ commit }, roleData) {
      return new Promise((resolve, reject) => {
        updateRole(roleData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    DeleteRole({ commit }, roleIds) {
      return new Promise((resolve, reject) => {
        deleteRole(roleIds)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default roleStore;



