import Vue from "vue";
import Router from "vue-router";
const _import = require("./_import_" + process.env.NODE_ENV);
// in development-env not use lazy-loading, because lazy-loading too many pages will cause webpack hot update too slow. so only in production use lazy-loading;
// detail: https://panjiachen.github.io/vue-element-admin-site/#/lazy-loading

Vue.use(Router);

/* Layout */
import Layout from "@/views/layout/Layout";

export const constantRouterMap = [
  { path: "/login", component: _import("auth/Login"), hidden: true },
  { path: "/404", component: _import("errorPage/404"), hidden: true },
  { path: "/401", component: _import("errorPage/401"), hidden: true },
  {
    path: "",
    component: Layout,
    redirect: "dashboard",
    children: [
      {
        path: "",
        component: _import("dashboard/Dashboard"),
        name: "dashboard",
        meta: { title: "dashboard", icon: "dashboard", noCache: true }
      }
    ]
  }
];

const router = new Router({
  mode: "history",
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition;
    }
    if (to.hash) {
      return { selector: to.hash };
    }
  },
  routes: constantRouterMap
});

export default router;

export const asyncRouterMap = [
  {
    path: "/pos",
    component: _import("pos/Pos"),
    children: [
      { path: "", name: "Pos", meta: { title: "pos", icon: "desktop" } }
    ]
  },
  {
    path: "/product",
    component: Layout, // redirect: "dashboard",
    redirect: "noredirect",
    meta: { title: "Product", icon: "cubes" },
    children: [
      {
        path: "",
        component: _import("product/Product"),
        name: "product",
        meta: { title: "Product", icon: "cubes" }
      },
      {
        path: ":id",
        component: _import("product/ProductDetail"),
        name: "productDetail",
        meta: { title: "Product Detail" },
        hidden: true
      },
      {
        path: "category",
        component: _import("product/ProductCategory"),
        name: "productCategory",
        meta: { title: "Category", icon: "tags" }
      }
    ]
  },
  {
    path: "/supplier",
    component: Layout,
    children: [
      {
        path: "",
        name: "Supplier",
        component: _import("supplier/Supplier"),
        meta: { title: "suppliers", icon: "truck" }
      }
    ]
  },
  {
    path: "/transaction",
    component: Layout,
    children: [
      {
        path: "",
        name: "Transaction",
        component: _import("transaction/Transaction"),
        meta: { title: "transaction", icon: "exchange" }
      }
    ]
  },
  {
    path: "/shop",
    component: Layout,
    redirect: "shop",
    meta: { title: "Shop", icon: "shopping-basket" },
    children: [
      {
        path: "",
        name: "shop",
        component: _import("shop/Shop"),
        meta: { title: "Shops", icon: "shopping-basket" }
      },
      {
        path: ":id",
        component: _import("shop/ShopDetail"),
        name: "shopDetail",
        meta: { title: "Shop Detail" },
        hidden: true
      }
    ]
  },
  {
    path: "/report",
    component: Layout,
    redirect: "noredirect",
    meta: { title: "Report", icon: "files-o" },
    children: [
      {
        path: "daily",
        component: _import("report/ReportDaily"),
        name: "reportDaily",
        meta: { title: "Report Daily", icon:"file-text" }
      },
      {
        path: "periodic",
        component: _import("report/ReportPeriodic"),
        name: "reportPeriodic",
        meta: { title: "Report Periodic", icon:"file-text" }
      }
    ]
  },
  {
    path: "/user",
    component: Layout,
    redirect: "noredirect",
    meta: { title: "User", icon: "user" },
    children: [
      {
        path: "index",
        name: "User",
        component: _import("user/User"),
        meta: { title: "users", icon: "user", noCache: true }
      },
      {
        path: ":id",
        component: _import("user/UserDetail"),
        name: "userDetail",
        meta: { title: "User Detail" },
        hidden: true
      }
    ]
  },
  {
    path: "/role",
    component: Layout,
    children: [
      {
        path: "",
        name: "Role",
        component: _import("role/Role"),
        meta: { title: "roles", icon: "bandcamp" }
      }
    ]
  } /* Add more route above... */,
  { path: "*", redirect: "/404", hidden: true }
];



