<template>
  <el-menu class="navbar" mode="horizontal">
    <hamburger class="hamburger-container" :toggleClick="toggleSidebar" :isActive="sidebar.isCollapse"></hamburger>

    <breadcrumb class="breadcrumb-container"></breadcrumb>

    <div class="right-menu">
      <el-tooltip effect="dark" :content="'screen full'" placement="bottom">
        <screenfull class="screenfull right-menu-item"></screenfull>
      </el-tooltip>

      <!-- <lang-select class="international right-menu-item"></lang-select> -->

      <el-dropdown class="avatar-container right-menu-item" trigger="click">
        <div class="avatar-wrapper">
          <img class="user-avatar" src="@/assets/droidtocat.png">
          <!-- <i class="el-icon-caret-bottom"></i> -->
        </div>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>{{username}}</el-dropdown-item>
          <el-dropdown-item divided>
            <div style="display:flex;align-items:center;">
              <icon name="power-off" style="margin-right:5px;"></icon>
              <span @click="logout">{{this.$t('navbar.logOut')}}</span>
            </div>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </el-menu>

</template>

<script>
import { mapGetters } from "vuex";
import Hamburger from "@/components/Hamburger/Hamburger";
import Breadcrumb from "@/components/Breadcrumb/Breadcrumb";
import Screenfull from "@/components/Screenfull/Screenfull";
import LangSelect from "@/components/LangSelect/LangSelect";

import { getIdentity } from "@/utils/auth";

export default {
  data() {
    return {
      avatar: [
        "https://wpimg.wallstcn.com/f778738c-e4f8-4870-b634-56703b4acafe.gif?imageView2/1/w/80/h/80",
        "@/assets/droidtocat.png"
      ],
      username: ""
    };
  },
  components: {
    Hamburger,
    Breadcrumb,
    Screenfull,
    LangSelect
  },
  computed: {
    ...mapGetters(["sidebar", ""])
  },
  methods: {
    toggleSidebar() {
      this.$store.dispatch("toggleSidebar");
    },
    logout() {
      this.$store.dispatch("Logout").then(() => {
        location.reload();
      });
    }
  },
  created() {
    const identity = getIdentity();
    this.username = identity.username
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.navbar {
  height: 50px;
  line-height: 50px;
  border-radius: 0px !important;
  .hamburger-container {
    line-height: 58px;
    height: 50px;
    float: left;
    padding: 0 10px;
  }
  .breadcrumb-container {
    float: left;
  }
  .errLog-container {
    display: inline-block;
    vertical-align: top;
  }
  .right-menu {
    float: right;
    height: 100%;
    &:focus {
      outline: none;
    }
    .right-menu-item {
      display: inline-block;
      margin: 0 8px;
    }
    .screenfull {
      height: 20px;
    }
    .international {
      vertical-align: top;
      cursor: pointer;
    }
    .theme-switch {
      vertical-align: 15px;
    }
    .avatar-container {
      height: 50px;
      margin-right: 30px;
      .avatar-wrapper {
        cursor: pointer;
        margin-top: 5px;
        position: relative;
        .user-avatar {
          width: 40px;
          height: 40px;
          border-radius: 10px;
        }
        .el-icon-caret-bottom {
          position: absolute;
          right: -20px;
          top: 25px;
          font-size: 12px;
        }
      }
    }
  }
}
</style>




