import request from "@/utils/request.js";

export function fetchAccesses() {
  return request({
    url: '/get/access',
    method: 'get',
  })
}

export function fetchAccess(accessId) {
  return request({
    url: '/get/access/'+accessId,
    method: 'get'
  })
}



