import request from "@/utils/request.js";
import urlSerializer from "@/utils/urlSerializer";

export function fetchSuppliers(urlParams) {
  const urlParams_s = urlSerializer(urlParams);
  return request({
    url: "/get/supplier?" + urlParams_s,
    method: "get"
  });
}

export function fetchSupplier(supplierId) {
  return request({
    url: "/get/supplier/" + supplierId,
    method: "get"
  });
}

export function addSupplier(supplierData) {
  const data = {
    ...supplierData
  };
  return request({
    url: "/create/supplier",
    method: "post",
    data
  });
}

export function updateSupplier(supplierData) {
  const { supplierId, ...data } = supplierData;
  return request({
    url: "/edit/supplier/" + supplierId,
    method: "put",
    data
  });
}

export function deleteSupplier(supplierIds) {
  const data = {
    supplier_ids: supplierIds
  };
  console.log("data: ", data);
  return request({
    url: "/delete/supplier/",
    method: "delete",
    data
  });
}



