import { LOGIN_TOKEN, IDENTITY } from "@/constants";

// const TokenKey = "Admin-Token";

export function getToken() {
  return localStorage.getItem(LOGIN_TOKEN);
}

export function setToken(token) {
  localStorage.setItem(LOGIN_TOKEN, token);
}

export function removeToken() {
  localStorage.removeItem(LOGIN_TOKEN);
}

export function setIdentity(identity) {
  return localStorage.setItem(IDENTITY, JSON.stringify(identity));
}

export function getIdentity() {
  return JSON.parse(localStorage.getItem(IDENTITY));
}

export function removeIdentity() {
  localStorage.removeItem(IDENTITY);
}

export function getUserShops() {
  return JSON.parse(localStorage.getItem(IDENTITY)).shops;
}

export function cleanCredential() {
  removeToken();
  removeIdentity();
}



