import { SIDEBAR_STATUS, LANGUAGE } from "@/constants";

const appStore = {
  state: {
    sidebar: {
      isCollapse: !+localStorage.getItem(SIDEBAR_STATUS)
    },
    language: localStorage.getItem(LANGUAGE) || 'en'
  },
  mutations: {
    TOGGLE_SIDEBAR: state => {
      if (state.sidebar.isCollapse) {
        localStorage.setItem(SIDEBAR_STATUS, 1)
      }else{
        localStorage.setItem(SIDEBAR_STATUS, 0)
      }
      state.sidebar.isCollapse = !state.sidebar.isCollapse
    },
    SET_LANGUAGE: (state, language) => {
      state.language = language
      localStorage.setItem(LANGUAGE, language)
    },
  },
  actions: {
    toggleSidebar({commit}) {
      console.log("isCollapse: ", localStorage.getItem(SIDEBAR_STATUS))
      commit('TOGGLE_SIDEBAR')
    },
    setLanguage({commit}, language) {
      commit('SET_LANGUAGE', language)
    },
  }
}

export default appStore;



