function currencyFmt(n, currency) {

  const num = n ? n : 0;

  return (
    currency +
    " " +
    num.toFixed(2).replace(/./g, function(c, i, a) {
      return i > 0 && c !== "." && (a.length - i) % 3 === 0 ? "," + c : c;
    })
  );
}

export function toIdr(value) {
  return currencyFmt(value, "Rp");
}



