import request from "@/utils/request.js";

export function generateReportDaily() {
  return request({
    url: "/generate/report/profit/daily",
    method: "get"
  });
}

export function generateReportPeriodic(payload) {
  return request({
    url: "/generate/report/profit/periodic",
    method: "post",
    data: payload
  });
}



