<template>

  <el-container>
    <sidebar class="sidebar-containerx"></sidebar>
    <el-container class="second-container">
      <navbar></navbar>

      <el-main>
        <transition name="slide" mode="out-in">
          <router-view></router-view>
        </transition>
      </el-main>

      <!-- <footbar></footbar> -->
    </el-container>
  </el-container>

</template>

<script>
import { Sidebar, Navbar, Footbar } from "./components";

export default {
  components: { Sidebar, Navbar, Footbar }
};
</script>

<style>
.el-container {
  height: 100%;
}

.second-container {
  height: 100%;
  flex-direction: column;
  width: 1px;
}
.el-header,
.el-footer {
  /* background-color: #b3c0d1; */
  color: #333;
  line-height: 60px;
}

.el-main {
  background-color: #fff; /* #e9eef3 */
  color: #333;
}
</style>




