import { loginByUsername, logout } from "@/api/auth";
import {
  fetchUsers,
  fetchUser,
  addUser,
  updateUser,
  deleteUser,
  fetchUserStatistic,
} from "@/api/user";
import { LOGIN_TOKEN, IDENTITY } from "@/constants";
import router from "@/router";
import {
  getToken,
  setToken,
  removeToken,
  setIdentity,
  removeIdentity
} from "@/utils/auth";
import { removeSelectedShop } from "@/utils/localStorage";

const userStore = {
  state: {
    token: getToken(),
    identity: null,
    roles: []
  },

  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token;
    },
    SET_IDENTITY: (state, user) => {
      state.identity = user;
    },
    SET_ROLES: (state, roles) => {
      state.roles = roles;
    }
  },

  actions: {
    LoginByUsername({ commit, dispatch }, authData) {
      const username = authData.username.trim();
      return new Promise((resolve, reject) => {
        loginByUsername(username, authData.password)
          .then(res => {
            const data = res.data;
            commit("SET_TOKEN", data.data.token);
            commit("SET_IDENTITY", data.data.user);

            // store token into localstorage
            setToken(data.data.token);
            setIdentity(data.data.user);

            resolve();
          })
          .catch(error => {
            console.log("LoginByUsername error: ", error.response);
            reject(error);
          });
      });
    },
    Logout({ commit }) {
      return new Promise((resolve, reject) => {
        logout()
          .then(() => {
            removeToken();
            removeIdentity();
            removeSelectedShop();
            commit("SET_TOKEN", "");
            // router.replace("/login");
            resolve();
          })
          .catch(error => {
            console.log("ERROR LOGOUT: ", error);
            // remove identity, selshop, token
            removeToken();
            removeIdentity();
            removeSelectedShop();
            commit("SET_TOKEN", "");

            reject(error);
          });
      });
    },
    FetchUsers({ commit, state }, urlParams = null) {
      return new Promise((resolve, reject) => {
        fetchUsers(urlParams)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    FetchUser({ commit }, userId) {
      return new Promise((resolve, reject) => {
        fetchUser(userId)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    AddUser({ commit }, userData) {
      return new Promise((resolve, reject) => {
        addUser(userData)
          .then(res => {
            console.log("Success add user, res: ", res);
            resolve(res);
          })
          .catch(error => {
            console.log("Failed add user, error: ", error.response);
            reject(error);
          });
      });
    },
    UpdateUser({ commit }, userData) {
      return new Promise((resolve, reject) => {
        updateUser(userData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    DeleteUser({ commit }, userIds) {
      return new Promise((resolve, reject) => {
        deleteUser(userIds)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    GetUserInfo({ commit }, userId) {
      return new Promise((resolve, reject) => {
        fetchUser(userId)
          .then(res => {
            const data = res.data.data;
            // const roles = data.user.roles;
            const roles = ["admin"]; // FIXME: have to fix
            commit("SET_ROLES", roles);

            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    FetchUserStatistic({ commit }, userId) {
      return new Promise((resolve, reject) => {
        fetchUserStatistic(userId)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default userStore;



