import {
  fetchProductCategories,
  fetchProductCategory,
  addProductCategory,
  updateProductCategory,
  deleteProductCategory
} from "@/api/productCategory";
import { LOGIN_TOKEN } from "@/constants";
import router from "@/router";

const productCategoryStore = {
  state: {},

  mutations: {},

  actions: {
    FetchProductCategories({ commit, state }, urlParams=null) {
      return new Promise((resolve, reject) => {
        fetchProductCategories(urlParams)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    AddProductCategory({ commit }, productCategoryData) {
      return new Promise((resolve, reject) => {
        addProductCategory(productCategoryData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    UpdateProductCategory({ commit }, productCategoryData) {
      return new Promise((resolve, reject) => {
        updateProductCategory(productCategoryData)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    DeleteProductCategory({ commit }, productCategoryIds) {
      return new Promise((resolve, reject) => {
        deleteProductCategory(productCategoryIds)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default productCategoryStore;



