import { generateReportDaily, generateReportPeriodic } from "@/api/report";

const reportStore = {
  namespaced: true,

  state: {},

  mutations: {},

  actions: {
    GenerateReportDaily({ commit, state }) {
      return new Promise((resolve, reject) => {
        generateReportDaily()
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    },
    GenerateReportPeriodic({ commit, state }, payload) {
      return new Promise((resolve, reject) => {
        generateReportPeriodic(payload)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default reportStore;



