import { fetchDashboard } from "@/api/dashboard";

const dashboardStore = {
  namespaced: true,

  state: {},

  mutations: {},

  actions: {
    FetchDashboard({ commit, state }, urlParams = null) {
      return new Promise((resolve, reject) => {
        fetchDashboard(urlParams)
          .then(res => {
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default dashboardStore;



