// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from "vue";

// Vendor
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import Icon from "vue-awesome/components/Icon";
import "vue-awesome/icons";
import money from "v-money";
import "@/styles/index.scss";
import "element-ui/lib/theme-chalk/display.css";
import VueMoment from "vue-moment";
// App
import App from "./App";
import router from "./router";
import store from "./store";
import i18n from "./lang";
import filters from "./filters";
import firebase from "firebase";

import "./permission";

// ------------------------------FIREBASE---------------------------------
// Initialize Firebase
// TODO: Replace with your project's customized code snippet
var config = {
  apiKey:
    "BBPEqEQpsvsEwG7zMArzoP4u7YVHzPz3cKYA1lJfvEH9ZP2JU7U_sgM7Isah06BIdhHupzJewMin0s2LOZ4kEZU",
  messagingSenderId: "559442159097"
};
firebase.initializeApp(config);

const messaging = firebase.messaging();
messaging.usePublicVapidKey(
  "BBPEqEQpsvsEwG7zMArzoP4u7YVHzPz3cKYA1lJfvEH9ZP2JU7U_sgM7Isah06BIdhHupzJewMin0s2LOZ4kEZU"
);

messaging
  .requestPermission()
  .then(function() {
    console.log("Notification permission granted.");
    // TODO(developer): Retrieve an Instance ID token for use with FCM.
    // ...

    // Get Instance ID token. Initially this makes a network call, once retrieved
    // subsequent calls to getToken will return from cache.
    messaging
      .getToken()
      .then(function(currentToken) {
        console.log("currentToken outside ", currentToken);
        if (currentToken) {
          console.log("current token: ", currentToken);
          sendTokenToServer(currentToken);
          updateUIForPushEnabled(currentToken);
        } else {
          // Show permission request.
          console.log(
            "No Instance ID token available. Request permission to generate one."
          );
          // Show permission UI.
          updateUIForPushPermissionRequired();
          setTokenSentToServer(false);
        }
      })
      .catch(function(err) {
        console.log("An error occurred while retrieving token. ", err);
        // showToken("Error retrieving Instance ID token. ", err);
        setTokenSentToServer(false);
      });
  })
  .catch(function(err) {
    console.log("Unable to get permission to notify.", err);
  });

// Callback fired if Instance ID token is updated.
messaging.onTokenRefresh(function() {
  messaging
    .getToken()
    .then(function(refreshedToken) {
      console.log("Token refreshed.", refreshedToken);
      // Indicate that the new Instance ID token has not yet been sent to the
      // app server.
      setTokenSentToServer(false);
      // Send Instance ID token to app server.
      sendTokenToServer(refreshedToken);
      // ...
    })
    .catch(function(err) {
      console.log("Unable to retrieve refreshed token ", err);
      // showToken("Unable to retrieve refreshed token ", err);
    });
});

function setTokenSentToServer(sent) {
  console.log("call setTokenSentToServer ", sent);
  window.localStorage.setItem("sentToServer", sent ? "1" : "0");
}

// Send the Instance ID token your application server, so that it can:
// - send messages back to this app
// - subscribe/unsubscribe the token from topics
function sendTokenToServer(currentToken) {
  console.log("call sendTokenToServer ", currentToken);
  if (!isTokenSentToServer()) {
    console.log("Sending token to server...");
    // TODO(developer): Send the current token to your server.
    setTokenSentToServer(true);
  } else {
    console.log(
      "Token already sent to server so won't send it again " +
        "unless it changes"
    );
  }
}

function updateUIForPushEnabled(currentToken) {
  console.log("call updateUIForPushEnabled ", currentToken);
  // showHideDiv(tokenDivId, true);
  // showHideDiv(permissionDivId, false);
  // showToken(currentToken);
}

function updateUIForPushPermissionRequired() {
  console.log("call updateUIForPushPermissionRequired ");
  // showHideDiv(tokenDivId, false);
  // showHideDiv(permissionDivId, true);
}

// ------------------------------FIREBASE---------------------------------

// Moment
Vue.use(VueMoment);

// Global filter
Vue.use(filters);

// Register directive v-money and component <money>
Vue.use(money, { precision: 2 });

// Register Element UI
Vue.use(ElementUI, {
  size: "medium", // set element-ui default size
  i18n: (key, value) => i18n.t(key, value)
});

// Register Icon
Vue.component("icon", Icon);
Vue.config.productionTip = false;

// Create Vue Instance
new Vue({
  el: "#app",
  router,
  store,
  i18n,
  components: { App },
  template: "<App/>"
});



