import request from "@/utils/request.js";
import urlSerializer from "@/utils/urlSerializer";

export function fetchShops(urlParams) {
  const urlParams_s = urlSerializer(urlParams);
  return request({
    url: '/get/shop?' + urlParams_s,
    method: 'get',
  })
}

export function fetchShop(shopId) {
  return request({
    url: '/get/shop/'+shopId,
    method: 'get'
  })
}

export function addShop(shopData) {
  const data = {
    ...shopData
  }
  return request({
    url: '/create/shop',
    method: 'post',
    data,
  })
}

export function updateShop(shopData) {
  const {shopId, ...data} = shopData
  return request({
    url: '/edit/shop/'+shopId,
    method: 'put',
    data,
  })
}

export function deleteShop(shopIds) {
  const data = {
    shop_ids: shopIds
  }
  console.log("data: ", data);
  return request({
    url: '/delete/shop/',
    method: 'delete',
    data,
  })
}

export function fetchShopStatistic(shopId) {
  return request({
    url: '/get/shop/' + shopId + '/statistic',
    method: 'get'
  })
}



