import request from "@/utils/request.js";
import urlSerializer from "@/utils/urlSerializer";

export function fetchProductCategories(urlParams) {
  const urlParams_s = urlSerializer(urlParams);
  return request({
    url: '/get/product-category?' + urlParams_s,
    method: 'get',
  })
}

export function fetchProductCategory(categoryId) {
  return request({
    url: '/get/product-category/'+categoryId,
    method: 'get'
  })
}

export function addProductCategory(productCategoryData) {
  const data = {
    ...productCategoryData
  }
  return request({
    url: '/create/product-category',
    method: 'post',
    data,
  })
}

export function updateProductCategory(productCategoryData) {
  const {categoryId, ...data} = productCategoryData
  return request({
    url: '/edit/product-category/'+categoryId,
    method: 'put',
    data,
  })
}

export function deleteProductCategory(categoryIds) {
  const data = {
    productCategory_ids: categoryIds
  }
  console.log("data: ", data);
  return request({
    url: '/delete/product-category/',
    method: 'delete',
    data,
  })
}



