import request from "@/utils/request.js";
import urlSerializer from "@/utils/urlSerializer";

export function fetchProducts(urlParams) {
  const urlParams_s = urlSerializer(urlParams);
  return request({
    url: "/get/product?" + urlParams_s,
    method: "get"
  });
}

export function fetchProduct(productId) {
  return request({
    url: "/get/product/" + productId,
    method: "get"
  });
}

export function addProduct(productData) {
  const data = {
    ...productData
  };
  return request({
    url: "/create/product",
    method: "post",
    data
  });
}

export function updateProduct(productData) {
  const { productId, ...data } = productData;
  return request({
    url: "/edit/product/" + productId,
    method: "put",
    data
  });
}

export function deleteProduct(productIds) {
  const data = {
    product_ids: productIds
  };
  console.log("data: ", data);
  return request({
    url: "/delete/product/",
    method: "delete",
    data
  });
}

export function fetchProductStatistic(productId) {
  return request({
    url: "/get/product/" + productId + "/statistic",
    method: "get"
  });
}



