import request from "@/utils/request.js";
import urlSerializer from "@/utils/urlSerializer";

export function fetchPosProducts(urlParams) {
  const urlParams_s = urlSerializer(urlParams);
  return request({
    url: "/get/pos/product?" + urlParams_s,
    method: "get"
  });
}

export function sellProduct(payload) {
  return request({
    url: "/sell/product",
    method: "post",
    data: payload
  });
}

/**
 * CALCULATION LOGIC
 */
export function calcTotalPrice(qty, selectedPrice) {
  return qty * selectedPrice;
}

export function calcDiscountValue(totalPrice, discount) {
  let _discountValue = 0;

  if (discount) {
    if (discount.type === "percent") {
      _discountValue = totalPrice * discount.value / 100;
    } else {
      _discountValue = discount.value;
    }
  }

  return _discountValue;
}

export function calcAfterDiscount(totalPrice, discountValue) {
  return totalPrice - discountValue;
}

export function calcTotalOrderValue(orders) {
  const totalOrders = orders.map(order => {
    const totalPrice = calcTotalPrice(order.qty, order.selectedPrice);
    const discountValue = calcDiscountValue(totalPrice, order.discount);
    return calcAfterDiscount(totalPrice, discountValue);
  });

  const totalOrderValue = totalOrders.reduce((acc, val) => {
    return acc + val;
  }, 0);

  return totalOrderValue;
}



